.menu{
    display: flex;
    justify-content: space-between;
    font-size : 3em;
    height: 68px;
    color: #FF6060;
}
a{
    text-decoration: none;
    color: #FF6060;
}
.nav{
    display: flex;
    justify-content: space-around;
    width: 20%;
    font-size: 20px;
    text-decoration: none;
}
.logokasa{
    Height : 60%;
}
li{
    list-style: none;
}
.activeLink{
    text-decoration: underline;
}
@media screen and (max-width: 992px){
    .nav{
        width: 30%;
    }
}
@media screen and (max-width: 767px){
    .nav{
        width: 50%;
    }
}